.dialog-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:space-around;

  height: 10%;
  max-height: 35px;
  width: 100%;

  margin: 0% 0% 1% 0%;
  padding: 0% 0.5% 0.5% 0.5%;
  box-sizing: border-box;

  border-bottom: 1px solid;
}

.user-active {
  color: #32CD32;
}

.user-inactive {
  color: #8B0000;
}