.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  height: 10svh;
  min-height: 45px;
  max-height: 55px;
  width: 100%;

  margin: 1% 0%;
  padding: 0% 2%;
  box-sizing: border-box;

  border: 1px solid;
  border-radius: 5px;
}

.content-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  height: 100%;
  width: 80%;

  box-sizing: border-box;
  margin-right: 1%;

  overflow-x: hidden;
}

.content-wrapper>:nth-child(1) {
  width: 70%;

  box-sizing: border-box;
  margin-right: 5%;
  text-wrap: nowrap;
  overflow: hidden;
}

.content-wrapper>:nth-child(2) {
  width: 20%;

  box-sizing: border-box;
  margin-right: 5%;
  text-wrap: nowrap;
}

@media screen and (width < 500px) {
  .content-wrapper {
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .content-wrapper>:nth-child(1) {
    width: 95%;
  }

  .content-wrapper>:nth-child(2) {
    width: 95%;
  }
}