.context-menu {
  position: absolute;
  width: 100%;
  max-width: 200px;

  margin: 0;
  padding: 1%;
  box-sizing: border-box;

  list-style: none;
  background: #F0F0F0;
  border-radius: 5px;
  border: 1px solid;
}

.context-menu_item {
  width: 100%;
  padding: 1% 5%;
  box-sizing: border-box;

  border-radius: 5px;
  background: #F0F0F0;
}

.context-menu_item:hover {
  cursor: pointer;
  background: #FDFDFD;
}