.user-contact-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  width: 100%;

  padding: 1% 5%;

  box-sizing: border-box;

  border-radius: 2px;
}

.user-contact-container:hover {
  cursor: pointer;

  background: #F5F5F5;
}

.user-status {
  height: 6px;
  width: 6px;

  margin: 0% 5% 0% 0%;

  box-sizing: border-box;

  border-radius: 50%;
  border: 1px solid;

  background: #8B0000;
}
.user-status.active {
  background: #32CD32;
}

.user-login {
  width: 75%;
  overflow-x: hidden;

  margin: 0% 2% 0% 0%;
  box-sizing: border-box;
}

.user-login:hover {
  cursor: pointer;
}

.user-messages {
  width: 5%;
  min-width: 15px;

  text-align: center;
  font-size: 10px;

  border-radius: 50%;
  border: 1px solid #32CD32;
}