.dialog-input {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  height: 15%;
  max-height: 35px;
  width: 100%;

  margin: 1% 0% 0% 0%;
  padding: 0.5% 0.5% 0% 0.5%;
  box-sizing: border-box;

  border-top: 1px solid;
}

.dialog-input-field {
  height: 28px;
  width: 70%;

  padding: 0% 5%;
  outline: none;
}

.dialog-input-cancel {
  position: relative;
  right: 24px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  height: 14px;
  width: 14px;

  border-radius: 50%;
  border: 1px solid;
  font-size: 0.6rem;
}

.dialog-input-cancel:hover {
  cursor: pointer;
}

.dialog-input-button {
  width: 10%;
}

@media (max-height: 500px) {
  .dialog-input-field {
    height: 20px;
  }
}