.message-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  width: 100%;
  margin: 1% 0%;
  box-sizing: border-box;
}

.current {
  align-items: flex-end;
}

.another {
  align-items: flex-start;
}

.message {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  width: 60%;
  padding: 1%;
  box-sizing: border-box;

  background: #F5F5F5;
  border-radius: 5px;
}

.current .message:hover {
  cursor: pointer;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  width: 80%;

  font-size: 0.7rem;
}

.header>:nth-child(1) {
  margin-right: 10%;
}

.text {
  width: 100%;

  margin: 2% 0% 0% 0%;
  padding: 3%;
  box-sizing: border-box;

  text-align: justify;

  border-radius: 5px;
  background: #FDFDFD;
}

.footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  width: 80%;

  font-size: 0.7rem;
}