.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  height: 100%;
  width: 35%;

  margin: 1% 0%;
  padding: 0.5%;
  box-sizing: border-box;

  border: 1px solid;
  border-radius: 5px;
}

.search {
  height: 24px;
  width: 83%;  

  padding: 1% 5%;
  outline: none;
}

@media (max-height: 500px) {
  .search {
    height: 14px;
  }
}