.dialog-content {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  height: 80%;
  width: 100%;

  padding: 1%;
  box-sizing: border-box;

  overflow-y: scroll;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  height: 100%;

  text-align: center;
}

.history-delimier {
  position: relative;
  width: 100%;

  font-size: 0.7rem;
  text-align: center;
}

.history-delimier::before {
  content: ' ';
  position: absolute;
  left: 0px;
  top: 8px;
  width: 100%;
  border-bottom: 1px solid;

  background: red;
}

.delimier-text {
  position: relative;

  margin: 1%;
  padding: 0% 5%;
  box-sizing: border-box;

  background: #FFFFFF;
}